import { useState } from "react";
import Image from "next/image";
import Link from "next/link";

export default function ReturnToGeorgia() {
  const [isRotating, setIsRotating] = useState(false);

  const handleClick = () => {
    setIsRotating(true); // დავიწყოთ ბრუნვა
    setTimeout(() => setIsRotating(false), 2000); // 2 წამის შემდეგ გაჩერდეს
  };

  return (
    <Link href="/" className="flex items-center justify-center space-x-2 relative">
      {/* მარცხენა დროშა */}
      <Image 
        src="/georgia-flag.png" 
        alt="Georgia Flag" 
        width={30} 
        height={25} 
        className="rounded-none"
      />

      {/* მოძრავი ტექსტი */}
      <span
        className={`text-white font-bold text-lg transition-transform duration-700 ${
          isRotating ? "animate-spin" : ""
        }`}
        onClick={handleClick}
      >
        დამაბრუნე
      </span>

      {/* მარჯვენა დროშა */}
      <Image 
        src="/georgia-flag.png" 
        alt="Georgia Flag" 
        width={30} 
        height={25} 
        className="rounded-none"
      />
    </Link>
  );
}
